@font-face {
  font-family: "Poppins300";
  src: url(../img/fonts/Poppins-Light.ttf);
}
@font-face {
  font-family: "Poppins600";
  src: url(../img/fonts/Poppins-SemiBold.ttf);
}
* {
  margin: 0;
  padding: 0;
  border: 0;
  line-height: 1.4em;
  vertical-align: baseline;
  text-decoration: none;
  outline: 0;
  box-sizing: border-box;
  background-color: transparent;
  font-family: "Poppins300", Helvetica, Arial, Lucida, sans-serif;
  font-size: 16px;
  color: #666666;
}

body {
  background-color: #F1F1F1;
}

main {
  width: 80%;
  max-width: 1200px;
  min-height: 100vh;
  margin: auto;
}
main h3 {
  width: 100%;
  margin-top: 100px;
  margin-bottom: 36px;
  font-family: "Poppins600", Helvetica, Arial, Lucida, sans-serif;
  font-size: 35px;
  color: #FE7320;
  text-align: center;
}
main .filters-container {
  display: flex;
  align-content: flex-start;
  flex-wrap: wrap;
  width: 100%;
}
main .filters-container .wrapper {
  height: fit-content;
  display: flex;
  gap: 24px;
  margin: 12px 0;
}
main .filters-container .wrapper.one {
  width: 79%;
  order: 1;
}
main .filters-container .wrapper.two {
  width: 21%;
  order: 2;
}
main .filters-container .wrapper.three {
  width: 100%;
  order: 3;
}
main .filters-container .wrapper.four {
  width: 100%;
  height: fit-content;
  order: 3;
}
main .filters-container .wrapper.five {
  width: 100%;
  margin-top: 36px;
  display: flex;
  justify-content: center;
  order: 4;
}
main .filters-container .wrapper .drop-down-container {
  position: relative;
  height: 50px;
}
main .filters-container .wrapper .drop-down-container.city {
  width: 27%;
  z-index: 10;
}
main .filters-container .wrapper .drop-down-container.distance {
  width: 20%;
  margin-right: 12px;
  z-index: 9;
}
main .filters-container .wrapper .drop-down-container.sector {
  width: 25%;
  z-index: 8;
}
main .filters-container .wrapper .drop-down-container.employment-form {
  width: 25%;
  z-index: 7;
}
main .filters-container .wrapper .drop-down-container.working-hours {
  width: 25%;
  z-index: 6;
}
main .filters-container .wrapper .drop-down-container.language {
  width: 25%;
  z-index: 5;
}
main .filters-container .wrapper .element {
  width: 100%;
  height: 50px;
  display: flex;
  align-items: center;
}
main .filters-container .wrapper .element img.arrow-down {
  position: absolute;
  right: 10px;
  top: 15px;
  height: 20px;
  display: flex;
}
main .filters-container .wrapper .element img.arrow-down.big {
  height: 26px;
  right: -32px;
}
main .filters-container .wrapper .element.checkbox-container {
  display: flex;
  align-items: center;
}
main .filters-container .wrapper .element.checkbox-container .checkbox {
  display: block;
  position: relative;
  height: 35px;
  width: 35px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  /* Hide the browser's default checkbox */
  /* Create a custom checkbox */
  /* On mouse-over, add a background color */
  /* When the checkbox is checked, add a background */
  /* When the checkbox is checked, add a background */
  /* Create the checkmark/indicator (hidden when not checked) */
  /* Show the checkmark when checked */
  /* Style the checkmark/indicator */
}
main .filters-container .wrapper .element.checkbox-container .checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
}
main .filters-container .wrapper .element.checkbox-container .checkbox .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
  border-radius: 8px;
}
main .filters-container .wrapper .element.checkbox-container .checkbox input:checked ~ .checkmark {
  background-color: #FE7320;
}
main .filters-container .wrapper .element.checkbox-container .checkbox .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
main .filters-container .wrapper .element.checkbox-container .checkbox input:checked ~ .checkmark:after {
  display: block;
}
main .filters-container .wrapper .element.checkbox-container .checkbox .checkmark:after {
  left: 10px;
  top: 4px;
  width: 10px;
  height: 19px;
  border: solid white;
  border-width: 0 4px 4px 0;
  transform: rotate(40deg);
}
main .filters-container .wrapper .element.checkbox-container .label-text {
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  margin: 0 12px;
}
main .filters-container .wrapper .element.drop-down {
  width: 100%;
  height: fit-content;
  min-height: 50px;
  position: absolute;
  align-items: flex-start;
  padding-left: 20px;
  padding-right: 5px;
  overflow-y: hidden;
  border-radius: 15px;
  font-size: 16px;
  color: #666666;
  background-color: #fff;
}
main .filters-container .wrapper .element.drop-down .list-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list-title {
  height: 50px;
  display: flex;
  align-items: center;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list {
  width: 100%;
  min-height: fit-content;
  max-height: 0;
  display: flex;
  overflow-y: auto;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list.expand {
  max-height: 400px;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list::-webkit-scrollbar {
  width: 6px;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list::-webkit-scrollbar-track {
  border-radius: 3px;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list::-webkit-scrollbar-thumb {
  background: rgba(206, 206, 206, 0.5568627451);
  border-radius: 3px;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list::-webkit-scrollbar-thumb:hover {
  background: #CECECE;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list ul {
  list-style-type: none;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list ul li {
  margin: 12px 0;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small {
  display: flex;
  align-items: center;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small .checkbox {
  display: block;
  position: relative;
  height: 24px;
  width: 24px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  flex-shrink: 0;
  /* Hide the browser's default checkbox */
  /* Create a custom checkbox */
  /* On mouse-over, add a background color */
  /* When the checkbox is checked, add a background */
  /* When the checkbox is checked, add a background */
  /* Create the checkmark/indicator (hidden when not checked) */
  /* Show the checkmark when checked */
  /* Style the checkmark/indicator */
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small .checkbox input {
  position: absolute;
  opacity: 0;
  cursor: pointer;
  height: 100%;
  width: 100%;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small .checkbox .checkmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  background-color: #fff;
  border: 1px rgba(206, 206, 206, 0.5568627451) solid;
  border-radius: 8px;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small .checkbox input:checked ~ .checkmark {
  background-color: #FE7320;
  border: 1px #FE7320 solid;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small .checkbox .checkmark:after {
  content: "";
  position: absolute;
  display: none;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small .checkbox input:checked ~ .checkmark:after {
  display: block;
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small .checkbox .checkmark:after {
  left: 7px;
  top: 3px;
  width: 5px;
  height: 12px;
  border: solid white;
  border-width: 0 3px 3px 0;
  transform: rotate(40deg);
}
main .filters-container .wrapper .element.drop-down .list-wrapper .list .checkbox-container-small .label-text {
  display: inline-block;
  font-size: 16px;
  cursor: pointer;
  margin: 0 8px;
}
main .filters-container .wrapper .element.job {
  width: 53%;
}
main .filters-container .wrapper .element.search-btn {
  width: 100%;
  margin-left: 12px;
  display: flex;
  justify-content: center;
  background-color: #FE7320;
  color: white;
  font-family: "Poppins600", Helvetica, Arial, Lucida, sans-serif;
  font-size: 25px;
  border-radius: 15px;
}
main .filters-container .wrapper .element.remote {
  width: 190px;
}
main .filters-container .wrapper .element.relocation {
  width: 190px;
}
main .filters-container .wrapper .element.advanced-search-btn {
  position: relative;
  width: fit-content;
  font-size: 25px;
}
main .filters-container .wrapper .element.advanced-search-btn .sliders {
  height: 26px;
  margin-right: 8px;
}
main .filters-container .wrapper .element input {
  width: 100%;
  height: 100%;
  padding: 0 20px;
  background-color: #fff;
  border-radius: 15px;
}
main .filters-container .wrapper .element input::placeholder {
  font-size: 16px;
  color: #666666;
  opacity: 1;
}
main .results {
  margin: 25px 0;
}
main .awarded {
  margin: 25px 25px 50px 25px;
}
main .awarded .result .top .branche {
  margin-left: 10px;
}
main .awarded .result .ribbon-wrap {
  width: 75px;
  height: 75px;
  overflow: hidden;
  position: absolute;
  border-radius: 2px;
  top: -8px;
  left: -8px;
}
main .awarded .result .ribbon-wrap .ribbon {
  width: 125px;
  font-size: 5px;
  text-align: center;
  padding: 2px 0;
  background: #ff2166;
  color: #fff;
  position: absolute;
  transform: rotate(-45deg);
  left: -31px;
  top: 23px;
}
main .awarded .result .ribbon-wrap .ribbon img {
  height: 11px;
}
main .awarded .result .ribbon-wrap::before, main .awarded .result .ribbon-wrap::after {
  content: "";
  width: 10px;
  height: 10px;
  background: #bb1c4e;
  position: absolute;
  z-index: -1;
}
main .awarded .result .ribbon-wrap::before {
  top: 0;
  right: 0;
}
main .awarded .result .ribbon-wrap::after {
  bottom: 0;
  left: 0;
}
main .result {
  background-color: #FFFFFF;
  margin: 20px 0;
  padding: 20px;
  border-radius: 15px;
  border-top-right-radius: 15px;
  position: relative;
  border: solid white 2px;
  color: #666666;
}
main .result .top {
  min-height: 50%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
}
main .result .top .lang {
  position: absolute;
  top: 3px;
  right: 3px;
  width: 26px;
  height: 26px;
  font-size: 14px;
  margin: 5px;
  border-radius: 50%;
  border: 1px solid #bebebe;
}
main .result .top .lang p {
  height: 21px;
  opacity: 1;
  text-transform: uppercase;
  color: #bebebe;
  position: absolute;
  left: 50%;
  top: 45%;
  transform: translate(-50%, -50%);
  display: flex;
}
main .result .top .branche-name {
  min-width: 40px;
  display: flex;
  flex-direction: column;
  height: 100%;
}
main .result .top .branche-name .branche {
  height: 50%;
  font-weight: 300;
  font-size: 14px;
  display: flex;
  justify-content: left;
  align-items: flex-end;
  line-height: 1.2em;
  padding: 0 5px;
}
main .result .top .branche-name .name {
  display: flex;
  justify-content: left;
  align-items: flex-start;
  height: 50%;
  font-weight: 500;
  font-size: 20px;
  line-height: 1.2em;
  padding: 0 5px;
  overflow: hidden;
}
main .result .bottom {
  min-height: 50%;
  display: flex;
  justify-content: left;
  align-items: flex-end;
  flex-wrap: wrap;
}
main .result .bottom .city-container .city {
  display: flex;
  align-items: center;
  font-weight: 300;
  padding: 0 5px;
  font-size: 14px;
  margin-right: 5px;
  margin-top: 10px;
}
main .result .bottom .city-container .city img {
  height: 14px;
  padding-right: 5px;
}
main .result .bottom .details-container {
  display: flex;
  flex-wrap: wrap;
}
main .result .bottom .details-container .remote {
  padding: 0 5px;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.2em;
  margin: 0 10px;
  margin-top: 10px;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(251, 80, 180, 0.1294117647);
  display: flex;
  align-items: center;
}
main .result .bottom .details-container .relocation {
  padding: 0 5px;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.2em;
  margin: 0 10px;
  margin-top: 10px;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(255, 128, 0, 0.1411764706);
  display: flex;
  align-items: center;
}
main .result .bottom .details-container .jobtype {
  font-weight: 400;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1.2em;
  margin: 0 10px;
  margin-top: 10px;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(34, 157, 251, 0.1215686275);
}
main .result .bottom .details-container .salary {
  font-weight: 400;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
  line-height: 1.2em;
  margin: 0 10px;
  margin-top: 10px;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(34, 139, 34, 0.1843137255);
}
main .result .bottom .details-container .employmentform {
  display: flex;
  flex-wrap: wrap;
}
main .result .bottom .details-container .employmentform p {
  padding: 0 5px;
  font-weight: 400;
  font-size: 12px;
  line-height: 1.2em;
  margin: 0 10px;
  margin-top: 10px;
  display: flex;
  align-items: center;
  height: 20px;
  padding: 2px 10px;
  border-radius: 3px;
  background-color: rgba(119, 136, 153, 0.1921568627);
}
main .pages-container {
  display: flex;
  justify-content: flex-end;
  align-items: flex-start;
  margin: 25px 50px;
  position: relative;
  opacity: 1;
  flex-wrap: wrap;
}
main .pages-container .pages {
  display: flex;
  justify-content: center;
  margin-bottom: 30px;
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
main .pages-container .pages .page {
  display: inline-block;
  border-radius: 3px;
  border: 1px solid #b3b3b3;
  color: #505050;
  text-align: center;
  line-height: 30px;
  width: 30px;
  height: 30px;
  cursor: pointer;
  margin: 4px;
}
main .pages-container .pages .page.active {
  background-color: #FE7320;
  border: 1px solid #FE7320;
  color: white;
}
main .pages-container .pages .page.active:hover {
  background-color: #1962b3;
  border: 1px solid #1962b3;
}
main .pages-container .pages .page:hover {
  background-color: rgba(254, 115, 32, 0.2196078431);
}
main .pages-container .pages .prev,
main .pages-container .pages .next {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
}
main .pages-container .pages .prev img,
main .pages-container .pages .next img {
  height: 18px;
  opacity: 0.4;
}
main .pages-container .pages .prev.active,
main .pages-container .pages .next.active {
  cursor: pointer;
}
main .pages-container .records-on-page-container {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
}
main .pages-container .records-on-page-container select.records-on-page {
  background-color: white;
  color: #666666;
  padding: 5px;
  border-radius: 3px;
  border: 1px solid #FE7320;
}
main .pages-container .records-on-page-container select.records-on-page option.placeholder {
  color: #D3D3D3;
}
main .pages-container .records-on-page-container .label {
  font-size: 11px;
  margin-top: 2px;
}
main .pages-container.active {
  opacity: 1;
}

@media (max-width: 1200px) {
  main {
    max-width: 750px;
  }
  main h3 {
    margin-top: 90px;
  }
  main .filters-container .wrapper.one {
    width: 100%;
    order: 1;
    flex-wrap: wrap;
    justify-content: space-between;
  }
  main .filters-container .wrapper.two {
    width: 100%;
    order: 4;
    display: flex;
    justify-content: center;
  }
  main .filters-container .wrapper.three {
    width: 100%;
    order: 2;
    justify-content: center;
  }
  main .filters-container .wrapper.four {
    width: 100%;
    order: 2;
    flex-wrap: wrap;
  }
  main .filters-container .wrapper.five {
    order: 3;
    width: 100%;
  }
  main .filters-container .wrapper .drop-down-container.city {
    width: 45%;
    flex-grow: 1;
  }
  main .filters-container .wrapper .drop-down-container.distance {
    margin-right: 0;
    width: 45%;
    flex-grow: 1;
  }
  main .filters-container .wrapper .drop-down-container.search-btn {
    max-width: 30%;
    min-width: 220px;
    margin-left: 0;
  }
  main .filters-container .wrapper .drop-down-container.sector {
    width: 45%;
    flex-grow: 1;
    order: 1;
  }
  main .filters-container .wrapper .drop-down-container.employment-form {
    width: 45%;
    flex-grow: 1;
    order: 3;
  }
  main .filters-container .wrapper .drop-down-container.working-hours {
    width: 45%;
    flex-grow: 1;
    order: 2;
  }
  main .filters-container .wrapper .drop-down-container.language {
    width: 45%;
    flex-grow: 1;
    order: 4;
  }
  main .filters-container .wrapper .element.job {
    width: 100%;
    flex-shrink: 0;
  }
}
@media (max-width: 600px) {
  main {
    max-width: 375px;
  }
  main h3 {
    font-size: 25px;
    margin-top: 36px;
  }
  main .filters-container .wrapper .arrow-down.big {
    height: 20px;
    right: -26px;
  }
  main .filters-container .wrapper .sliders {
    height: 20px;
    margin-right: 4px;
  }
  main .filters-container .wrapper.three {
    flex-wrap: wrap;
    gap: 0;
    margin: 0;
  }
  main .filters-container .wrapper.five {
    margin-top: 0;
    margin-bottom: 0;
  }
  main .filters-container .wrapper .drop-down-container.city {
    width: 52%;
  }
  main .filters-container .wrapper .drop-down-container.distance {
    width: 32%;
  }
  main .filters-container .wrapper .drop-down-container.search-btn {
    width: 100%;
    max-width: unset;
  }
  main .filters-container .wrapper .drop-down-container.remote {
    width: 100%;
  }
  main .filters-container .wrapper .drop-down-container.relocation {
    width: 100%;
  }
  main .filters-container .wrapper .drop-down-container.sector {
    width: 100%;
  }
  main .filters-container .wrapper .drop-down-container.employment-form {
    width: 100%;
  }
  main .filters-container .wrapper .drop-down-container.working-hours {
    width: 100%;
  }
  main .filters-container .wrapper .drop-down-container.language {
    width: 100%;
  }
  main .filters-container .wrapper .element.advanced-search-btn {
    font-size: 16px;
  }
  main .filters-container .wrapper .element.advanced-search-btn .sliders {
    height: 20px;
    margin-right: 8px;
  }
  main .filters-container .wrapper .element.advanced-search-btn .arrow-down.big {
    height: 20px;
    right: -28px;
  }
}
@media (max-width: 480px) {
  main .filters-container .wrapper .drop-down-container.city {
    width: 100%;
  }
  main .filters-container .wrapper .drop-down-container.distance {
    width: 100%;
  }
}